.transaction {
	border-bottom: 1px solid #ececec;
	padding: 20px 0;
	height: 90px;
	width: 100%;
	display: flex;
	flex-wrap: nowrap;
	align-items: center;

	&:hover {
		transition-property: background-color;
		transition-duration: .5s;
		background-color: #ececec;
	}

	.icon {
		width: 100px;
		order: 0;

		> .placeholder-icon {
			width: 40px;
			height: 40px;
			margin: 0 auto;
			border-radius: 100px;
			border: 0px solid #fff;
			background-color: #37a261;
			content: "EG";
			text-transform: uppercase;
			font-weight: 600;
		}

		> .merchant-icon {
			width: 40px;
			height: 40px;
			margin: 0 auto;
			background-size: contain;
			border-radius: 5px;
		}
	}

	.info {
		order: 1;
		h6 {
			text-transform: none;
			font-size: 20px;
			font-weight: 400;
			margin: 0;
		}
		p {
			font-size: 12px;
			font-weight: 400;
			margin: 0;
			margin-top: 5px;
		}
	}

	> .monies {
		order: 2;
		position: absolute;
		right: 20px;

		ul {
			display: flex;
			list-style-type: none;
			text-align: right;
			float: right;
			margin-left: 15px;

			&.is-load {
				color: #37a261;
			}

			> .monies-non-decimal,
			  .monies-direction {
				font-size: 28px;
				font-weight: 300;
			}
			> .monies-decimal {
				font-size: 15px;
				font-weight: 400;
				padding-top: 13px;
			}
		}
	}
}
